div {
  box-sizing: border-box;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.black-nav {
  background: black;
  width: 100%;
  display: flex;
  color: white;
  padding: 20px;
}

.btn_nice {
  margin: 20px;
  background-color: #1395e0;
  padding: 2px;
  border-radius: 2px;
  cursor: pointer;
}

.btn_asc {
  margin: 20px 20px 20px 0;
  background-color: #FFC83D;
  padding: 2px;
  border-radius: 2px;
  cursor: pointer;
}

.list {
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid grey;
}

.list_title {
  cursor: pointer;
}

.list_title:hover {
  color: #FFC83D;
}

.list_title:hover span {
  color: #000;
}

.modal {
  margin-top: 20px;
  padding: 20px;
  background: #eee;
  text-align: left;
}